	module.exports = {"data":{"markdownRemark":{"html":"<p>In the early morning, I read the \"Golden Sexuality Report\", which is a full translation for advertising effects. The Kinsey report is an important report on sex, but this book seems to be a book that answers people's sexual problems, not the report of Kinsey himself, but the report of the Kinsey Institute. At present, the head of the Jinsai Research Institute is a female professor. From the content and style of the book, we can also see this point, which is more delicate and enlightening.</p>\n<p>The last part of the book on contemporary Western political philosophy begins with the name \"Humanized Democracy in a Humanized Society.\" The development of Western democratic politics shows the inherent contradictions of democratic politics. I call this \"logical rebellion\". The arguments are:</p>\n<p>Social politics consists of the collective activities of each member of society. Political power comes from every member of this group. Therefore, the existence and activities of each member of society constitute the basic premise of democratic politics. Nothing can be discussed about democracy. However, in the process of the development of democratic politics, it shows an opposite force, that is, the power of democratic political operation crowds more and more members of society outside the political process, whether for technical reasons or For non-technical reasons, this process is indeed evolving and growing.</p>\n<p>From the basic purpose of proposing democracy, democratic politics is to promote the value of life and the individual's life. Because the choice of democracy in value is that politics should meet the requirements of human life and should represent the value of human life. Democracy is considered to be the most political way to advance the value of human life. However, in the actual political process, this has been greatly exceeded. Democracy itself has become democratic for democracy, or for the benefit of democracy, for ideology and democracy, not for human life. The output of the Western political system throughout the Cold War and the post-Cold War period fully demonstrates this. Other than that, from the establishment of a huge arsenal and military power, it is enough to prove that the original logic of democratic politics has been surpassed.</p>\n<p>There is another logic of democratic politics: democratic politics is a political mode that is most compatible with human existence, a political form that can be unified with human inner life, and a unity of external power and internal choice. Political way. But in fact, democratic politics itself has become more and more divorced from the human being, and has developed into a huge political machine. It has become a member of the external society and the existence of a class that has power. It seems that no one is controlling this political machine. But the political machine is in control. This is already a serious phenomenon in developed countries.</p>\n<p>One of the worries of contemporary Western political thinkers is this \"logical rebellion\". Their political philosophy is largely an explanation of this \"logical rebellion\" or a solution to the \"logical rebellion.\"</p>","id":"/home/swain/workspace/political-life/src/pages/en/0073.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0073.md","frontmatter":{"title":"April 10th Sunday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0073.md absPath of file >>> MarkdownRemark"}}

